/**
 * WordPress dependencies
 */
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { last, uniq } from 'lodash';
import { hasHead } from './functions';

export function getElementLabel( selector: string ): string {
	if ( ! selector ) {
		return LABELS.Element;
	}

	if ( 0 <= selector.indexOf( ',' ) ) {
		const labels = uniq( selector.split( ',' ).map( getElementLabel ) );
		return hasHead( labels ) ? labels[ 0 ] : LABELS.Element;
	}

	selector = cleanCssSelector( selector );
	if ( selector === '*' ) {
		return LABELS.AllElements;
	}

	if ( selector === ':root' ) {
		return LABELS.Root;
	}

	let lastSelector = last( getSelectorArray( selector ) ) ?? '';
	lastSelector = lastSelector.trim();
	if ( lastSelector.includes( '.' ) ) {
		lastSelector = '.' + lastSelector.split( '.' )[ 1 ];
	}
	if ( lastSelector.includes( '#' ) ) {
		lastSelector = '#' + lastSelector.split( '#' )[ 1 ];
	}
	if ( lastSelector.includes( ':' ) ) {
		lastSelector = lastSelector.split( ':' )[ 0 ] as string;
	}

	const tagName: string = getTagName( lastSelector ).toUpperCase();
	const elementId: string = getElementId( lastSelector ).toUpperCase();
	const className: string = getClassName( lastSelector ).toUpperCase();

	// eslint-disable-next-line @wordpress/no-unused-vars-before-return
	const cleanedClassName = `${ lastSelector } ${ elementId } ${ className }`
		.replace( /-/g, ' ' )
		.replace( /\s+/g, ' ' )
		.replace( /[^a-zA-Z0-9\s]/g, '' )
		.trim();

	if ( tagName === 'BODY' ) {
		return LABELS.Body;
	}
	if ( /\bWIDGET\b/.test( cleanedClassName ) ) {
		return LABELS.Widget;
	}
	if ( /\b(MENU|MAIN-MENU|NAVIGATION|NAV)\b/.test( cleanedClassName ) ) {
		return LABELS.Menu;
	}
	if ( /\b(FA|FA-(.*?))\b/.test( className ) ) {
		return LABELS.FontIcon;
	}
	if ( /\bSUBMIT\b/.test( cleanedClassName ) && tagName === 'INPUT' ) {
		return LABELS.SubmitButton;
	}
	if ( /\bMENUITEM\b/.test( cleanedClassName ) ) {
		return LABELS.MenuItem;
	}
	if ( /\b(ENTRYMETA|ENTRYMETABOX|POSTMETABOX)\b/.test( cleanedClassName ) ) {
		return LABELS.PostMetaDivision;
	}
	if ( /\bCOMMENTREPLYTITLE\b/.test( cleanedClassName ) ) {
		return LABELS.CommentReplyTitle;
	}
	if ( /\bLOGGEDINAS\b/.test( cleanedClassName ) ) {
		return LABELS.LoginInfo;
	}
	if ( /\bFORMALLOWEDTAGS\b/.test( cleanedClassName ) ) {
		return LABELS.AllowedTagsSection;
	}
	if ( /\bLOGO\b/.test( cleanedClassName ) ) {
		return LABELS.Logo;
	}
	if ( /\b(ENTRYTITLE|POSTTITLE)\b/.test( cleanedClassName ) ) {
		return LABELS.PostTitle;
	}
	if ( /\bCOMMENTFORM\b/.test( cleanedClassName ) ) {
		return LABELS.CommentForm;
	}
	if ( /\bWIDGETTITLE\b/.test( cleanedClassName ) ) {
		return LABELS.WidgetTitle;
	}
	if ( /\bTAGCLOUD\b/.test( cleanedClassName ) ) {
		return LABELS.TagCloud;
	}
	if ( /\bROW\b/.test( cleanedClassName ) ) {
		return LABELS.Row;
	}
	if ( /\b(BUTTON|BTN)\b/.test( cleanedClassName ) ) {
		return LABELS.Button;
	}
	if ( /\bLEAD\b/.test( cleanedClassName ) ) {
		return LABELS.Lead;
	}
	if ( /\bWELL\b/.test( cleanedClassName ) ) {
		return LABELS.Well;
	}
	if ( /\bACCORDIONTOGGLE\b/.test( cleanedClassName ) ) {
		return LABELS.AccordionToggle;
	}
	if ( /\bPANELBODY\b/.test( cleanedClassName ) ) {
		return LABELS.AccordionContent;
	}
	if ( /\bALERT\b/.test( cleanedClassName ) ) {
		return LABELS.AlertDivision;
	}
	if ( /\bMORELINK\b/.test( cleanedClassName ) ) {
		return LABELS.ShowMoreLink;
	}
	if (
		/\b(MENULINK|MENUICON|MENUBTN|MENUBUTTON)\b/.test( cleanedClassName )
	) {
		return LABELS.MenuLink;
	}
	if ( /\bSUBMENU\b/.test( cleanedClassName ) ) {
		return LABELS.SubMenu;
	}
	if ( /\b(POSTBODY|POST)\b/.test( cleanedClassName ) ) {
		return LABELS.PostDivision;
	}
	if ( /\b(CONTENT|DEFAULTCONTENT)\b/.test( cleanedClassName ) ) {
		return LABELS.ContentDivision;
	}
	if ( /\bENTRYCONTENT\b/.test( cleanedClassName ) ) {
		return LABELS.EntryContent;
	}
	if ( /\bENTRYFOOTER\b/.test( cleanedClassName ) ) {
		return LABELS.EntryFooter;
	}
	if ( /\bENTRYHEADER\b/.test( cleanedClassName ) ) {
		return LABELS.EntryHeader;
	}
	if ( /\bENTRYTIME\b/.test( cleanedClassName ) ) {
		return LABELS.EntryTime;
	}
	if ( /\bPOSTEDITLINK\b/.test( cleanedClassName ) ) {
		return LABELS.PostEditLink;
	}
	if ( /\bPOSTTHUMBNAIL\b/.test( cleanedClassName ) ) {
		return LABELS.PostThumbnail;
	}
	if ( /\bTHUMBNAIL\b/.test( cleanedClassName ) ) {
		return LABELS.Thumbnail;
	}
	if ( /ATTACHMENT/.test( className ) ) {
		return LABELS.ThumbnailImage;
	}
	if ( /\bEDITLINK\b/.test( cleanedClassName ) ) {
		return LABELS.EditLink;
	}
	if ( /\bCOMMENTSLINK\b/.test( cleanedClassName ) ) {
		return LABELS.CommentsLinkDivision;
	}
	if ( /\bSITEDESCRIPTION\b/.test( cleanedClassName ) ) {
		return LABELS.SiteDescription;
	}

	if ( ! isUndefined( className ) ) {
		const match = className.match(
			/([a-zA-Z0-9_-]+)?(SPAN|SMALL|MEDIUM|LARGE|COL|COLUMN|[_-]L|[_-]M|[_-]S|COL-XS|COL-MD|COL-LG|-COL-SM)([_-])?[0-9]+/
		);
		if ( ! isUndefined( match ) && ! /SECTION/.test( className ) ) {
			const colNumber = parseInt(
				( match as RegExpMatchArray )[ 0 ].replace( /\D/g, '' )
			);
			if ( colNumber > 0 && colNumber < 13 ) {
				return LABELS.Column.replace( '%d', `${ colNumber }` );
			}
		}
	}

	// Process CSS class name if applicable
	const processedClassName = processCssClassName( lastSelector );
	if ( processedClassName && /(#|\.)/.test( lastSelector ) ) {
		return processedClassName;
	}

	// Check if the last selector is a known semantic HTML tag
	const generalTags: Record< string, string > = {
		H1: LABELS.Heading,
		H2: LABELS.Heading,
		H3: LABELS.Heading,
		H4: LABELS.Heading,
		H5: LABELS.Heading,
		H6: LABELS.Heading,
		P: LABELS.Paragraph,
		IMG: LABELS.Image,
		A: LABELS.Link,
		LI: LABELS.ListItem,
		UL: LABELS.List,
		HEADER: LABELS.Header,
		FOOTER: LABELS.Footer,
		ARTICLE: LABELS.Article,
		CODE: LABELS.CodeTag,
		FORM: LABELS.FormDivision,
		LABEL: LABELS.Label,
		OL: LABELS.List,
		BLOCKQUOTE: LABELS.BlockQuote,
		NAV: LABELS.Navigation,
	};

	if (
		Object.keys( generalTags ).includes( lastSelector.toUpperCase() ) &&
		! /(#|\.)/.test( lastSelector )
	) {
		return generalTags[ lastSelector.toUpperCase() ] as string;
	}

	// Check for common class-based element names
	if ( /\b(CONTAINER|WRAPPER)\b/.test( cleanedClassName ) ) {
		return LABELS.Wrapper;
	}

	// Check for tag-specific names
	const tagToElementMap: Record< string, string > = {
		BR: LABELS.LineBreak,
		HR: LABELS.HorizontalRule,
		PRE: LABELS.Preformatted,
		TABLE: LABELS.Table,
		TR: LABELS.TableRow,
		TD: LABELS.TableData,
		BUTTON: LABELS.Button,
		CENTER: LABELS.CentredBlock,
		DL: LABELS.DefinitionList,
		DT: LABELS.DefinitionTerm,
		DD: LABELS.DefinitionDescription,
		TEXTAREA: LABELS.TextArea,
		TBODY: LABELS.BodyOfTable,
		THEAD: LABELS.HeadOfTable,
		TFOOT: LABELS.FootOfTable,
		U: LABELS.UnderlineTextTag,
		Q: LABELS.Quotation,
		CITE: LABELS.CitationTag,
		TIME: LABELS.TimeTag,
		CAPTION: LABELS.CaptionOfTable,
		INPUT: LABELS.Input,
		SPAN: LABELS.SpanTag,
		B: LABELS.BoldTag,
		EM: LABELS.ItalicTag,
		I: LABELS.ItalicTag,
		STRONG: LABELS.StrongTag,
		SMALL: LABELS.SmallTag,
		DIV: LABELS.Division,
	};

	if ( tagToElementMap[ tagName ] ) {
		return tagToElementMap[ tagName ];
	}

	if ( generalTags[ tagName ] ) {
		return generalTags[ tagName ];
	}

	// Return a properly formatted tag name or "Unknown Element" if undefined
	return tagName
		? capitalizeFirstLetter(
				tagName.replace( /(_|-)/g, ' ' ).toLowerCase()
		  )
		: LABELS.Element;
}

