// =======
// HELPERS
// =======

function cleanCssSelector( selector: string ): string {
	// Remove standard CSS pseudo-classes
	selector = selector.replace(
		/\:(hover|focus|link|visited|active|checked|disabled|enabled|invalid|valid)/g,
		''
	);

	// Remove CSS pseudo-elements (single or double colon)
	selector = selector.replace(
		/:(:)?(after|before|first-letter|first-line|selection)/g,
		''
	);

	// Trim and return the cleaned selector
	return selector.trim();
}

function getSelectorArray( selector: string ) {
	// Clean up selector
	selector = selector
		.trim()
		.replace( /\s\s+/g, ' ' ) // Remove extra spaces
		.replace( /(\s)?([>+~,])(\s)?/g, '$2' ) // Remove spaces around ">,+,~"
		.replace( />/g, ' ' ) // Convert '>' to space
		.trim();

	// Split by space if there are multiple selectors
	const selectorArray = selector.length
		? selector.split( ' ' ).map( ( v ) => v.trim() )
		: [];

	// Add spaces around '~' and '+' and return the result
	return selectorArray.map( ( v ) =>
		v.replace( /\~/g, ' ~ ' ).replace( /\+/g, ' + ' )
	);
}

function processCssClassName( input: string | undefined ) {
	if ( typeof input === 'undefined' ) {
		return false;
	}

	// Remove CSS class and ID prefixes
	input = input.replace( /[#.]/g, '' );

	// Convert CamelCase to kebab-case and lowercase everything
	input = input.replace( /([A-Z][a-z])/g, '-$1' ).toLowerCase();

	const hasUppercase = /[A-Z]/;
	const hasHyphen = /-/;

	if (
		hasHyphen.test( input ) &&
		( input.match( /(-|_)/g ) || [] ).length >= 5
	) {
		return false;
	}

	// Replace underscores and hyphens with spaces
	input = input.replace( /_/g, ' ' ).replace( /-/g, ' ' );

	// Final validation based on length and uppercase presence
	if ( hasUppercase.test( input ) || input.length < 3 || input.length > 50 ) {
		return false;
	}

	// Process the cleaned-up string
	return filterAndFormatString( input );
}

function filterAndFormatString( input: string ): string {
	const words = input.split( ' ' ); // Split input into an array of words
	const filteredWords: string[] = [];

	for ( const word of words ) {
		if ( word.length > 28 ) {
			continue;
		}

		// Ignore certain keywords if multiple words exist
		if (
			words.length > 1 &&
			/\b(elementor|oxy|ast|uael|owl|avia)\b/gi.test( word )
		) {
			continue;
		}

		if ( /[AEIOU]/i.test( word ) && word.length > 2 ) {
			filteredWords.push( word ); // Keep words with vowels if > 2 characters
		} else if ( ! /[A-Z]/i.test( word ) ) {
			filteredWords.push( word ); // Keep words without uppercase letters
		} else if ( word.length > 3 ) {
			filteredWords.push( word ); // Keep words longer than 3 characters
		}
	}

	const result = filteredWords.join( ' ' ).trim();

	return result.length > 2 ? capitalizeFirstLetter( result ) : '';
}

function capitalizeFirstLetter( text: string ): string {
	return text.charAt( 0 ).toUpperCase() + text.slice( 1 );
}

function isUndefined( a: unknown ): boolean {
	return (
		typeof a === typeof undefined ||
		a === false ||
		a === '' ||
		a === ' ' ||
		a === 'undefined' ||
		a === null
	);
}

function getTagName( selector: string ): string {
	const candidate = getLastElement( selector );

	if ( candidate.startsWith( '#' ) || candidate.startsWith( '.' ) ) {
		return '';
	}

	return candidate.split( /[#.]/ )[ 0 ] ?? '';
}

function getElementId( selector: string ): string {
	const candidate = getLastElement( selector );
	const matches = candidate.match( /#([^.]+)/ ) || [];
	return matches[ 1 ] || '';
}

function getClassName( selector: string ): string {
	const candidate = getLastElement( selector );
	return candidate
		.replace( /^[^#.]+/, '' )
		.replace( /#[^.]+/g, '' )
		.replace( /\./g, ' ' )
		.trim();
}

function getLastElement( selector: string ): string {
	return (
		last(
			selector
				.replace( /\s+/g, ' ' )
				.replace( /:[a-z0-9-_]+(\([^)]*\))?/g, '' )
				.split( ' ' )
		) ?? ''
	).trim();
}
